@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(45deg, rgb(130, 57, 146), rgb(6, 9, 243));
  min-height: 100vh;
  font-family: 'Courier New', Courier, monospace;
  position: relative;
}

code {

}

*{margin:0;padding:0;-webkit-margin-before:0;margin-block-start:0;-webkit-margin-after:0;margin-block-end:0;box-sizing:border-box}header{font-size:calc(1.4rem + 5vw);color:#f9011d;text-shadow:0 0 10px #fff;padding:2rem;text-align:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}#watermark{position:absolute;right:0;bottom:0.05%;font-size:calc(1.5rem + 2vw);-webkit-user-select:none;-ms-user-select:none;user-select:none}#watermark #HXN{font-size:calc(3rem + 2vw);font-family:'Amatic SC', cursive;color:#f9011d}@media (min-width: 1000px){.App #watermark{bottom:2%}}@media (min-width: 1980px){#computer{min-height:75vh;min-width:25vw}}

@font-face{font-family:calcFont;src:url("/Calculator/static/media/digital-7 (mono).ae951a18.ttf")}#computer{height:75vh;max-width:450px;margin:auto;box-shadow:0 0 5px 5px black;border-radius:2%;display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:1.5fr repeat(5, 1fr)}#computer button{border-width:0.01rem;padding:0;font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;font-size:calc(1rem + 1vw);grid-area:span 1}#screen{grid-area:1/1/2/5;background-color:black;display:flex;flex-direction:column;justify-content:space-around;align-items:flex-end;padding:0 5%;overflow:hidden}#screen .calc{font-size:calc(1rem + 1.5vw);color:#cf9f36;margin:0;padding-bottom:2%;font-family:calcFont;text-align:right;overflow:hidden;white-space:nowrap}#screen .calc p{float:right}#screen #previousCalc{max-width:100%}#screen #currentCalc{max-width:92%}#screen #currentRow{display:flex;width:100%;justify-content:space-between;min-height:25%}#screen #equal{font-size:calc(2rem + 1.5vw)}.light{background-color:#f7d07b}.light:active{background-color:#ffd375e5 !important}.dense{background-color:#eea812}.dense:active{background-color:#ffca57e5 !important}@media (min-width: 1100px){.App #computer{min-height:530px}}@media (min-width: 1980px){#computer{min-height:55 !important;min-width:30vw !important}}

